export default {
	data() {
		return {
			classification: ["前端", "uniapp", "后端", "MySQL", "前端", "uniapp"],
			nowIndex: null,
			res: "我是主页信息",
			newsData: [],
			all: {
				color: "#E07A76",
				fontSize: 0.8 + "rem",
				lineHeight: 34 + "px"
			}
		}
	},
	onLoad() {
		this.getData()
	},
	methods: {
		getClass(e) {
			// 控制点击顶部tab栏点击变色,筛选不同内容
			this.nowIndex = e.currentTarget.id
			this.all.color = "black"
			const Cname = e.target.dataset.name
			this.getData(Cname)
		},
		navToSearch() {
			// 跳转到搜索页面
			uni.navigateTo({
				url: '../search/search'
			});
		},
		getData(Cname) {
			uni.showLoading({
				title: "努力加载中..."
			})
			uniCloud.callFunction({
				name: "test",
				data: {
					newClass: Cname
				},
				data: {
					Email: "2993863530@qq.com",
					password: "123456789"
				},
				success: (res) => {
					this.newsData = res.result.attention
					uni.hideLoading()
				},
				fail: (err) => {
					console.log(err);
					uni.hideLoading()
				}
			})
		},
		allClass() {
			this.nowIndex = null
			this.all.color = "#E07A76"
			this.getData()
		}
	}
}
